
<template>
  <div class="wrap">
    <div v-if="type===0">
      <img class="pageImg" src="../assets/10.jpg" alt />
    </div>
    <div v-if="type===1">
      <img class="pageImg" src="../assets/2.jpg" alt />
    </div>
    <div class="nav">
      <div class="navList">
        <div class="navItem navItem1" @click="togType">1</div>
        <div class="navItem navItem2" @click="togType">2</div>
      </div>
    </div>
    <div class="link link1"  @click="submit">重置</div>
    <cube-popup type="my-popup" ref="myPopup">
      My Popup Content 1
    </cube-popup>
  </div>
</template>
 
<script>

export default {
  data() {
    return {
      type: 0
    };
  },
  methods: {
    togType:function () {
      this.type = this.type===0 ? 1:0
    },
    submit:function () {
      const toast = this.$createToast({
        time: 1000,
        txt: 'Updated successfully',
        type: 'txt',
        mask:true,
        onTimeout: () => {
          window.history.back(-1)
        }
      })
      toast.show()
    }
  },
  created:function () {
    document.title="Reset Password"
  }
};
</script>
    
<style scoped>
.nav {
  position: absolute;
  top:0;
  bottom: auto;
  height: 6.8%;
}
.link1 {
  top: 44.5%;
  width: 100%;
  left: 0;
  height: 7%;
}
</style>