
<template>
  <div class="wrap">
    <div v-if="type===0">
      <img class="pageImg" src="../assets/12.jpg" alt />
      <!-- <div class="link link1"  @click="submit">重置</div> -->
    </div>
    <div v-if="type===1">
      <img class="pageImg" src="../assets/13.jpg" alt />
      <div class="btnBox">
        <router-link class="btn btn1" to="/shareImg">查看分享</router-link>
        <div class="btn btn2"  @click="submit">回收</div>
      </div>
      
    </div>
    <div class="nav">
      <div class="navList">
        <div class="navItem navItem1" @click="togType">1</div>
        <div class="navItem navItem2" @click="togType">2</div>
      </div>
    </div>
    
  </div>
</template>
 
<script>

export default {
  data() {
    return {
      type: 0
    };
  },
  methods: {
    togType:function () {
      this.type = this.type===0 ? 1:0
    },
    submit:function () {
      this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: '',
        content: 'Are you sure you want to recall this QR code?',
        confirmBtn: {
          text: 'confirm',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: 'cancel',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        }).show()
    }
  },
  created:function () {
    document.title="Adidas ID Card"
  }
};
</script>
    
