
<template>
  <div class="box">
    <div class="wrap">
      <div v-if="type===0">
        <img class="pageImg" src="../assets/5.jpg" alt />
        <div class="link link1" @click="showPop">
          分享
        </div>
        <router-link class="link link11" to="/store">店铺</router-link>
      </div>
      <div v-if="type===1">
        <img class="pageImg" src="../assets/17.png" alt />
        <div class="link link2" @click="resetPage2">
          回收
        </div>
        <div class="link link22" @click="showImg">
          详情
        </div>
      </div>
      <div v-if="type===2" >
        <img class="pageImg" @click="setType(3)" src="../assets/16.jpg" alt />
        <!-- <div class="link link3" >
          分享
        </div> -->
      </div>
      <div v-if="type===3">
        <img class="pageImg" src="../assets/15.jpg" alt />
        <div class="link link4" @click="showImg">
          确认
        </div>
        <div class="link link5" @click="resetPage">
          取消
        </div>
      </div>
      <img src="../assets/nav2.jpg" class="pageImg opacity" alt />
    </div>

    <div class="pop" v-if="isShowPop">
      <div class="popCot">
        <img class="pageImg" src="../assets/6.jpg" alt="">
        <div class="popList">
          <div class="popItem list popItem1" @click="setType(2)">1</div>
          <div class="popItem list popItem2">2</div>
          <div class="popItem list popItem3">3</div>
        </div>
        <div class="link link3" @click="hidePop">
          取消
        </div>
      </div>
    </div> 
    <div class="shareImgBox" v-if="isShowImg" @click="hideImg">
      <div class="shareImg">
        <img class="pageImg" src="../assets/11.jpg" alt="">
      </div>
    </div> 
    <div class="nav">
      <img src="../assets/nav2.jpg" alt />
      <div class="navList">
        <router-link class="navItem navItem1" to="/home">首页</router-link>
        <router-link class="navItem navItem2" to="/share">分享</router-link>
        <router-link class="navItem navItem3" to="/set">设置</router-link>
      </div>
    </div>
  </div>
</template>
 
<script>
// import { setInterval } from 'timers';

export default {
  data() {
    return {
      type: 0,
      isShowPop:false,
      isShowImg:false
    };
  },
  methods: {
    showPop:function () {
      this.isShowPop=true
    },
    hidePop:function () {
      this.isShowPop=false
    },
    showImg:function () {
      this.isShowImg=true
      this.isShowPop=false

    },
    hideImg:function () {
      this.type = 1;
      this.isShowImg=false
    },
    resetPage:function () {
      this.type = 0;
      this.isShowImg=false
      this.isShowPop=false
    },
    resetPage2:function () {
      this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: '',
        content: 'Are you sure you want to recall this QR code?',
        confirmBtn: {
          text: 'confirm',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: 'cancel',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: () => {
          this.type = 0;
          this.isShowImg=false
          this.isShowPop=false
        }
        }).show()
    },
    setType:function (type) {
      this.type = type;
      this.isShowImg=false
      this.isShowPop=false
    },
  },
  created:function () {
    document.title="QR code"
  }
};
</script>
    
<style scoped>
.pop,.shareImgBox{
    position: fixed;
    bottom: 0;
    left:0;
    right:0;
    top:0;
    background: rgba(0,0,0,0.5);
    z-index: 10;
}
.popCot{
    position: absolute;
    bottom: 0;
    left:0;
    width:100%;
}
.link1 {
  bottom: 8.7%;
  width: 100%;
  left: 0;
  height: 5.2%;
}
.link2 {
      bottom: 13%;
    width: 100%;
    left: 0;
    height: 4%;
}
.link3 {
  bottom: 0%;
  width: 100%;
  left: 0;
  height: 28%;
}
.link4{
  top: 57%;
  width: 40%;
  right: 10%;
  height: 6%;
}
.link5{
  top: 57%;
  width: 40%;
  left: 10%;
  height: 6%;
}
.link11{
  top: 62%;
  width: 80%;
  left: 10%;
  height: 10%;
}
.link22{
  top: 57%;
  width: 15%;
  right: 10%;
  height: 6%;
}
.popList{
  position: absolute;
  bottom: 28%;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  height: 60%;
  
}
.popItem{
  flex:1;
  opacity: 0;
}
.shareImgBox{
  overflow-y:auto;
}
</style>