
<template>
  <div class="box">
    <div class="wrap">
      <div v-if="tpye===0">
        <img class="pageImg" src="../assets/4.jpg" alt />
        <div class="link link1">The QR code will expire after {{time}} seconds</div>
      </div>
      <div v-if="tpye===1">
        <img class="pageImg" v-if="tpye===1" src="../assets/3.jpg" alt />
        <div class="link link2" @click="changeType">更改状态</div>
      </div>
      <!-- <router-link class="link link2" to="/sso">sso</router-link> -->
    </div>
    <img class="pageImg" style="opacity: 0;" src="../assets/nav1.jpg" alt />
    <div class="nav">
      <img src="../assets/nav1.jpg" alt />
      <div class="navList">
        <router-link class="navItem navItem1" to="/home">首页</router-link>
        <router-link class="navItem navItem2" to="/share">分享</router-link>
        <router-link class="navItem navItem3" to="/set">设置</router-link>
      </div>
    </div>
  </div>
</template>
 
<script>
// import { setInterval } from 'timers';
let initTime = 15;
export default {
  data() {
    return {
      tpye: 0,
      time: initTime
    };
  },
  methods: {
    start: function() {
      this.time = initTime;
      let timer = window.setInterval(() => {
        this.time--;
        if (this.time == 0) {
          this.tpye = 1;
          window.clearInterval(timer);
        }
      }, 1000);
    },
    changeType: function() {
      this.tpye = 0;
      this.start();
    },
  created:function () {
    document.title="Adidas ID Card"
  }

  },
  created: function() {
    this.start();

    window.console.log(window.location.hash)
    if(window.location.hash!=='#/home?id=1'){
      return false
    }
    this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        content: 'Do I need to turn on FACEid to log in quickly?',
        confirmBtn: {
          text: 'Open Face ID',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: 'Cancel',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
       
        }).show()
  }
};
</script>
    
<style scoped>
.link1 {
  top: 39.13%;
  width: 73.33%;
  left: 13.835%;
  height: 5.6%;
  background: #fef0f0;
  color: #ef4040;
  font-size: 0.2rem;
  opacity: 1;
  border-radius: 9999px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.link2 {
  top: 68%;
  width: 100%;
  left: 0;
  height: 8%;
}
</style>