import Vue from 'vue'
import Router from 'vue-router' // 引入vue-router
import login from './components/login.vue'
import home from './components/home.vue'
import user from './components/user.vue'
import share from './components/share.vue'
import shareImg from './components/shareImg.vue'
import sso from './components/sso.vue'
import setPassword from './components/setPassword.vue'
import set from './components/set.vue'
import log from './components/log.vue'
import store from './components/store.vue'

Vue.use(Router) //使用vue-router
 
export default new Router({
    routes:[
        {
            path:'/',
            name:'login',
            component:login, // 
        },
        {
            path:'/login',
            name:'login',
            component:login, // 
        },
        {
            path:'/home',
            name:'home',
            component:home, // 
        },
        {
            path:'/user',
            name:'user',
            component:user, // 
        },
        {
            path:'/share',
            name:'share',
            component:share, // 
        },
        {
            path:'/store',
            name:'store',
            component:store, // 
        },
        {
            path:'/shareImg',
            name:'shareImg',
            component:shareImg, // 
        },
        {
            path:'/sso',
            name:'sso',
            component:sso, // 
        },
        {
            path:'/setPassword',
            name:'setPassword',
            component:setPassword, // 
        },
        {
            path:'/set',
            name:'set',
            component:set, // 
        },
        {
            path:'/log',
            name:'log',
            component:log, // 
        }
    ]

})